/* eslint-disable no-underscore-dangle */
import { Injectable, isDevMode } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { filter } from 'rxjs/operators';
import { Game, GamePoint, NoteResult, QuestionType, SelectedPoint } from 'src/models/models';

@Injectable({
  providedIn: 'root'
})
export class GameService {

  private _pointSelected$ = new BehaviorSubject<SelectedPoint>(null);
  private _enablePoints$ = new BehaviorSubject<string[]>(null);
  private _disablePoints$ = new BehaviorSubject<string[]>(null);
  private _answerSelected$ = new BehaviorSubject<{
    result: NoteResult;
    answers?: string[];
  }>(null);
  private _gameSelected$ = new BehaviorSubject<Game>(null);

  private _pointExplored = new Subject<GamePoint>();

  private exploredPoint = {};

  constructor() { }

  get pointSelected$() { return this._pointSelected$.pipe(filter(i => !!i)); }
  get enablePoints$() { return this._enablePoints$.pipe(filter(i => !!i)); }
  get disablePoints$() { return this._disablePoints$.pipe(filter(i => !!i)); }

  get answerSelected$() { return this._answerSelected$.pipe(filter(i => !!i)); }
  get gameSelected$() {
    return this._gameSelected$
      .pipe(filter(i => !!i));
    // .asObservable();
  }

  pointSelected(point: SelectedPoint) {
    this._pointSelected$.next({
      ...point,
      explored: this.isExplored(point.point)
    });
  }

  // eslint-disable-next-line @typescript-eslint/member-ordering
  _game: Game;

  async getGame(): Promise<Game> {
    return this._game;
  }

  setGame(obj: Game) {
    this._game = obj;
    this._gameSelected$.next(obj);
  }

  isExplored(point: GamePoint) {
    return !!this.exploredPoint[point.name];
  }

  isCloseEnough(distance: number) {
    if (isDevMode()) {
      return true;
    }

    return distance < 50;
  }

  answerSelected(point: GamePoint<QuestionType>, result: NoteResult, selections?: string[]) {
    this._answerSelected$.next({
      result,
      answers: selections
    });
  }

  moveNext(point: GamePoint<QuestionType>) {
    if (point.body.enables) { this._enablePoints$.next(point.body.enables); }
    if (point.body.disables) { this._disablePoints$.next(point.body.disables); }

    this.exlorePoint(point);
  }

  private exlorePoint(point: GamePoint) {
    this.exploredPoint[point.name] = true;
    this._pointExplored.next(point);
  }
}
