import { Injectable } from '@angular/core';
import JSONCrush from 'jsoncrush';
import { Game } from 'src/models/models';
import { GameService } from 'src/services/game.service';

@Injectable({
  providedIn: 'root'
})
export class UtilsService {

  constructor(
    private gameService: GameService
  ) {
  }

  async getCompressedGame(game?: Game) {
    const obj = await (game || this.gameService.getGame());
    let str = JSON.stringify(obj);
    // str = lzwEncode(str);
    str = JSONCrush.crush(str);
    // return encodeURIComponent(str);
    return str;
  }

  async getDecompressedGame(str: string): Promise<Game> {
    // str = decodeURIComponent(str);
    // str = lzwDecode(str);
    str = JSONCrush.uncrush(str);
    return JSON.parse(str);
  }

  async loadStory(str: string) {
    const story = await this.getDecompressedGame(str);
    this.gameService.setGame(story);
  }
}
